.DLbody2 {
  transition: 0.3s;
  -webkit-perspective: 100px;
  transform-style: preserve-3d;
  /*transform: rotateY(90deg);*/
}

.stepFinish {
  perspective: 900px;
}

.asaras {
  position: relative;
  display: grid;
  perspective: 900px;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 100%;
}
.Fbody2 {
  position: absolute;
}

.FlowCont {
  top: -10vh;
}

.tocatoca {
  margin-top: 30px;
}

.FlowCont {
  align-items: center;
  display: grid;
  position: relative;
  justify-items: center;
  transform: rotateY(-90deg);
  display: none;
  /*display: none;*/
  /*transform: rotateY(-90deg);*/
}

.cambiarGrid {
  animation-name: cambiargrid;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes cambiargrid {
  50% {
    grid-template-rows: 1fr 15% !important;
  }

  100% {
    grid-template-rows: 1fr 15% !important;
  }
}

.desaparece1 {
  animation-name: desaparece;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes desaparece {
  0% {
    transform: rotateY(0deg);
  }

  65% {
    opacity: 1;
  }

  95% {
    opacity: 0;
  }

  100% {
    transform: rotateY(90deg);
  }
}

.eya {
  height: calc(100% - 20px);
  width: 100%;
}

.apareces1 {
  display: grid !important;
  animation-name: apareces;
  animation-duration: 0.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes apareces {
  50% {
    transform: rotateY(-90deg);
  }

  100% {
    transform: rotateY(0deg);
  }
}

.FolowTexts {
  text-align: center;
  position: absolute;
  width: 640px;
  top: -300px;
}

.Fimportant {
  font-size: 24px;
  color: #ffe500;
  font-weight: bold;
  margin-top: 0;
}

.Ftexto {
  color: white;
  font-size: 18px;
}

.DLmessage {
  transition: 0.3s;
  -webkit-perspective: 100px;
  transform-style: preserve-3d;
  /*transform: rotateY(90deg);*/
}


.bodyTcket{
  display: grid;
  grid-template-rows: auto 1fr;
}

.bodyTcket .darkTheme{
  padding: 20px 0;
  width: 100%;
}

@media (max-width: 768px) {

  .bodyTcket{
    padding: 0 20px;
    width: calc(100% - 80px);
  }

}